/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "FormComponent",
    request: "get something",
    style: {},
  }; // TODO: Adjust this

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  // const {} = props.barrelProps;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /** 
   * Yup schema
   ============================================================================ */
  const schema = yup.object({
    text1: yup.string().required(t("Text1 is required")),
  });

  /**
   * Functions
   ============================================================================ */
  const onSubmit = (data) => {
    console.log(data);
    if (data === null) return false;
    return sendRequest({ req: props.request, data });
  };

  /**
   * Form
   ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Card title={t("Component")}>
      <Form noValidate onSubmit={handleSubmit}>
        <Form.Group>
          <Form.Label>{t("TEXT1")}</Form.Label>
          <Form.Control
            type="string"
            placeholder={""}
            name="text1"
            value={values.text1}
            onChange={handleChange}
            isInvalid={!!errors.text1 && touched.text1}
          />
          <Form.Control.Feedback type="invalid">
            {errors.text1}
          </Form.Control.Feedback>
        </Form.Group>

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  /**
   * Formik return
   ============================================================================ */
  return (
    <Formik
      component={ComponentForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        text1: "",
      }}
    />
  );
};
